@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding:0;
}
body{
  background-color: rgb(241, 240, 240);

}
.app__body{
  flex-direction: row;
  display: flex;
}

.header{
    display: flex;
    max-width: 100%;
    justify-content: space-between;
    height: 45px;
    padding: 5px 15px;
    background-color: #ffffff;
    box-shadow: 0px 4px 15px -13px #000000;
}
.header__left{
    display: flex;

}

.header__left img{
    width: 68px;
}
.header__input{
    display: flex;
    align-items: center;
    margin-left: 10px;
    border-radius: 30px;
    background-color: #ebebee;
}
.header__input input{
    border: none;
    background-color: transparent;
    border-radius: 30px; 
    align-items: center;
    justify-content: center;
    padding: 5px;
    width: 200px;
    outline: none;
}
.header__input input::-webkit-input-placeholder{
    font-weight: 500;
}
.header__input input:-ms-input-placeholder{
    font-weight: 500;
}
.header__input input::placeholder{
    font-weight: 500;
}
.header__mid{
    display: flex;
    justify-content: center;
    align-items: center;
}
.header__right{
    display: flex;
}
.header__mid{
    display: flex;
    justify-content: space-between;
    width: 550px;
}
.header__right{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 270px;
}
.header__profile{
    display: flex;
    align-items: center;
    margin-right: 10px;
}
.s{
    color: black;
}
.header__profile p{
    margin-left: 10px;
    font-weight: 500;
    font-family: inherit;
}
.header__icon{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    border-radius: 20px;
    height: 40px;
    background-color: #ebebee;
    font-family: inherit;
    cursor: pointer;
}
.header__icon:hover{
    background-color:rgb(214, 212, 212)

}
.header__opt{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    width: 80px;
    height: 50px;
    cursor: pointer;
}
.header__opt:hover{
    background-color: rgb(236, 229, 229);
}

.header__opt-active{
    display: flex;
    border-bottom: 4px solid  #095fcf;
    align-items: center;
    justify-content: center;
    width: 80px;
    height: 50px;
    cursor: pointer;

}
.header__opt-active > .MuiSvgIcon-root{
    color: #095fcf;
    cursor: pointer;

}

.MuiSvgIcon-root{
    color:rgb(138, 136, 136)

}
.Chat{
    padding: 10px;
    overflow: hidden;
}
.Chat__Top{
    display: flex;
    align-items: center;
}
.Chat__Top p{
    margin-left: 7px;
    font-size: 15px;
}
.Chat__messages{
    width: 100%;
    height: 53vh;
    overflow-x: hidden;
}
.Chat__form{
    align-items: center;
}
form input{
    width: 220px;
    padding: 10px;
    border: none;
    outline: none;
    font-size: 15px;
    border-radius: 15px;
    margin-left: 20px;
    background-color: #dddcdc;
}

.Modal__cont{
    position: relative;
}
.Modal{
    position: fixed;
    background-color: #fff;
    bottom: 0; 
    width:320px;
    height: 450px;
    right: 50px;
    z-index: 1000;
    box-shadow: 0px 2px 15px -3px #000000;
}
.Modal button{
    float: right;
    margin: 10px 10px;
    height: 30px;
    width: 30px;
    border: none;
    outline: none;
   
    cursor: pointer;
    background: #fff;
}

.ChatMessage{
    margin: 15px 5px;
}
.Chatmessage__profile{
    display: flex;
    align-items: center;
}
.Chatmessage__profile p{
    display: flex;
    align-items: center;
    margin-left: 12px;
    height: 10px;
    padding: 10px;
    background-color: lightgray;
    border-radius: 15px;
    font-size: 15px;
}
.Chatmessage__info{
    display: flex;
    flex-direction: column;
}
.Chatmessage__text{
    font-size: 13px;
    margin-left: 15px;
    background-color: #fff;
    font-weight:500;
}

.sidebarRow{
    display: flex;
    align-items: center;
    padding: 20px 20px;
    width: 240px;
}
.sidebarRow__title{
    font-size: 15px;
    font-weight: 500;
    margin-left: 20px;
    flex:1 1;
    color: rgb(53, 52, 52);
}
.sidebarRow__icon{
    color:#095fcf;
    
    font-size: 25px !important;
}
.sidebarRow:hover{
    background-color: lightgray;
    cursor: pointer;
}

.sidebarRow:hover > .sidebarRow__title{
    font-weight: bold;
}
.sidebarRow.selected{
    background-color: lightgray;
}
/* .sidebarRow.selected > .sidebarRow__icon{
    color: red;
}
.sidebarRow:hover > .sidebarRow__icon{
    color: red;
} */
.sidebarRow.selected > .sidebarRow__title{
    font-weight: bold;

}


.stories{
    display: flex;
    width: 670px;
    flex-direction: column;
    align-items: center;
    margin-left: 80px;
}
.stories__cont{
    display: flex;
    justify-content: space-between;
    width: 600px;
}

.posts{
    display: flex;
    flex-direction: column;
    margin-top: 20px;
    width: 600px;
    border-radius: 10px;
    padding: 10px;
    height: 100px;
    box-shadow:3px 3px 15px -10px #000000;
    background-color: rgb(255, 255, 255);
}
.posts__top{
    display: flex;
    flex-direction: row;

}
.posts__input{
    outline: none;
    border: none;
    width: 210px;
    padding: 12px;
    background-color: rgb(233, 231, 229);
    border-radius: 20px;
    margin-left: 10px;
}

.data-btn{
    display: flex;
    width: 200px;
    background-color: rgb(233, 231, 229);
    border-radius: 13px;
    outline: none;
    align-items: center;
    justify-content: center;
    border: none;
    color:rgb(87, 86, 86);
}
.posts__url{
    outline: none;
    border: none;
    width: 150px;
    padding: 10px;
    background-color: rgb(233, 231, 229);
    border-radius: 20px;
    margin-left: 10px;
}
form{
    display: flex;
}
.btn{
    display: flex;
    width: 200px;
    padding: 15px;
    border-radius: 13px;
    outline: none;
    align-items: center;
    justify-content: center;
    border: none;
    color:rgb(87, 86, 86);
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
}
.btn:hover{
    background-color: rgb(233, 231, 229);

}
.posts__bottom{
    display: flex;
    justify-content: space-between;
    border-radius: 10px;
    margin: 0 auto;
    padding: 10px;
    width: 480px;
}
.MakePost{
    width: 600px;
    background-color: #fff;
    padding: 15px;
    margin-top: 20px;
    border-radius: 12px;
}
.MakePost__top{
    display: flex;
    
}
.MakePost__profile{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}
.MakePost__bottom img{
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}
.MakePost h3{
    margin-top: 10px;
    font-weight: 500;
}
.MakePost__profile h4{
    font-weight: 500;
}
.MakePost {
    width: 100%;
    border-radius: 15px;
    background-color: #fff;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
  }
  
  .MakePost__top {
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
  }
  
  .MakePost__avatar {
    margin-right: 10px;
  }
  
  .MakePost__topInfo > h3 {
    font-size: medium;
  }
  
  .MakePost__bottom {
    padding: 0 10px;
    word-break: break-word;
  }
 .MakePost__image {
    margin-top:10px;
  }
  .MakePost__image > img {
    width: 100%;
  }
  
  .MakePost__options {
    padding-top: 10px;
    border-top: 1px solid lightgray;
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color: gray;
    cursor: pointer;
    padding: 15px;
  }
  
  .MakePost__option {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1 1;
  }
  
  .MakePost__option > p {
    margin-left: 10px;
  }
  
  .MakePost__option:hover {
    background-color: #e6e8eb;
    border-radius: 10px;
  }
.StoriesRow__avatar{
    position:absolute;
    top:60px;
    left:10px;
    width: 35px !important;
    height: 35px !important;
    border-style: solid !important;
    border-width: 4px !important;
    border-color: rgb(53, 169, 226) !important;
    cursor: pointer;
}
.StoriesRow__cont{
    position: relative;
}
.StoriesRow__name{
    position:absolute;
    bottom:20px;
    color:rgb(255, 255, 255);
    font-weight: 600;
    cursor: pointer;
    left:15px;
    width: 40px;
}
.StoriesRow__img{
    width: 110px;
    border-radius: 12px;
    height: 200px;
    cursor: pointer;
}
.StoriesRowCol{
    display: flex;
    width: 670px;
    flex-direction: column;
    align-items: center;
}
.StoriesRowCol__cont{
    display: flex;
    justify-content: space-between;
    width: 600px;
}

.login {
    display: grid;
    place-items: center;
    height: 100vh;
  }
  
  .login > button {
    width: 300px;
    background-color: #2e81f4;
    color: #eff2f5;
    font-weight: 800;
  }
  
  login > button:hover {
    background-color: #fff;
    color: #2e81f4;
  }
  
  .login > div > img {
    object-fit: contain;
    height: 150px;
  }
  .login > div {
    display: flex;
    flex-direction: column;
  }
